@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700&family=Dancing+Script&family=Parisienne&family=Playfair+Display&family=Roboto:wght@300&family=Tilt+Prism&display=swap");

body {
  background-color: #272733;
}

.bg-color {
  background-color: #272733;
}

.font-roboto {
  font-family: "Roboto", sans-serif;
}

.blur-2023 {
  background: rgba(255, 255, 255, 0.5);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.blur-container {
  position: relative;
}

.blur-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px); /* Add your desired blur effect here */
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease, backdrop-filter 0.3s ease,
    transform 0.3s ease;
  transition-delay: 0.2s; /* Delay the transition */
  display: flex;
  justify-content: center;
  align-items: center;
}

.blur-container:hover .blur-overlay {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
  transition-delay: 0s; /* Remove the delay on hover */
}

.font-cinzel {
  font-family: "Cinzel Decorative", cursive;
}

.font-playfair {
  font-family: "Playfair Display", serif;
}

/* Apply scrollbar styles to all scrollbars on the webpage */
*::-webkit-scrollbar {
  width: 10px;
  height: 5px;
}

*::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.597);
  border-radius: 5px;
}

*::-webkit-scrollbar-track {
  background: rgba(238, 238, 238, 0);
}

.cart22 {
  animation: ani-change 10s infinite ease-out;
  height: 3px;
}

@keyframes ani-change {
  0% {
    width: 0;
  }
  
  50% {
    width: 100%;
    background-color: #f8717195;
  }

  100% {
    width: 0%;
    display: hidden;
  }
}
